.keyboardContainer {
  position: relative;
}

.keyboardContainer .swipeCanvasElement {
  position:absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  z-index: 999;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.simple-keyboard.hg-theme-default.module-simplekeyboardswipe .hg-button {
  height: 60px;
}

.simple-keyboard.module-simplekeyboardswipe .hg-button span {
  pointer-events: none;
}
